@use "../../index.scss";
.Input{
  @extend .view;
  &__input{
    width: 85vw;
    height: 28px;
    margin-bottom: 2px;
    border:1px solid #18ab29 ;
    border-radius: 5px;
  }
  
  &__label{
    margin-top: 16px;
    align-self: flex-start;
  }
  &__errorMsg{
    color: red;
    align-self: flex-start;
  }
}